import * as React from 'react';
import { css } from 'office-ui-fabric-react';

import styles from './MobilePreviewDeviceTypeSelector.module.scss';
import { DeviceType } from '../../mobilePreview/MobilePreview';

export interface IMobilePreviewDeviceTypeSelectorProps {
  onChangeDeviceType: (type: DeviceType) => void;
}

export default class MobilePreviewDeviceTypeSelector extends React.Component
  <IMobilePreviewDeviceTypeSelectorProps, void> {

  public render(): React.ReactElement<{}> {
    const spanClassNameMobile: string = css('ms-Icon', 'ms-Icon--mobile', 'ms-fontColor-neutralTertiary',
      styles.mobilePreviewDeviceIcon);
    const spanClassNameTablet: string = css('ms-Icon', 'ms-Icon--tablet', 'ms-fontColor-neutralTertiary',
      styles.mobilePreviewDeviceIcon);

    return(
    <div>
      <li className={ styles.navBarItemRight }>
        <a onClick={ () => this.props.onChangeDeviceType(DeviceType.Phone) }>
          <span className={ spanClassNameMobile }></span>
        </a>
      </li>
      <li className={ styles.navBarItemRight }>
        <a onClick={ () => this.props.onChangeDeviceType(DeviceType.Tablet) }>
          <span className={ spanClassNameTablet }></span>
        </a>
      </li>
    </div>
    );
  }
}